<template>
  <div class="app">
    <ProductList
        v-if="view === 'list'"
        :initialCategory="currentCategory"
        @view-detail="loadDetail"
        @update-category="currentCategory = $event"
    />

    <ProductDetail
        v-else-if="view === 'detail' && detailData"
        :data="detailData"
        @go-back="view = 'list'"
    />
  </div>
</template>

<script setup>
import { ref } from 'vue';
import axios from 'axios';
import ProductList from './components/ProductList.vue';
import ProductDetail from './components/ProductDetail.vue';

const view = ref('list');
const detailData = ref(null);
// 2. ÌòÑÏû¨ Ïπ¥ÌÖåÍ≥†Î¶¨ ÏÉÅÌÉúÎ•º Î∂ÄÎ™®ÏóêÏÑú Í¥ÄÎ¶¨ (Í∏∞Î≥∏Í∞í Îç∞Ïä§ÌÅ¨ÌÉë)
const currentCategory = ref('Îç∞Ïä§ÌÅ¨ÌÉë');

const loadDetail = async (code, isNew) => {
  const apiPath = isNew ? '/api/new/product' : '/api/product';
  const res = await axios.get(`http://localhost:8083${apiPath}/${code}`);
  detailData.value = res.data;
  view.value = 'detail';
};

</script>